body {
  font-family: "Roboto", Arial, Helvetica, sans-serif;
  color: #1a1a1a;
  background-color: #ecefde;
  font-size: 14px;
  line-height: 1.5em;
  font-weight: 300; }

.container {
  max-width: 1060px;
  width: 100%;
  margin: 0 auto;
  position: relative;
  padding: 0 20px; }

.btn {
  cursor: pointer;
  background-color: #232323;
  color: #ecefde;
  transition: color 0.3s ease background-color 0.3s ease;
  border-radius: 2px;
  padding: 5px 16px;
  box-shadow: none;
  border: none;
  text-decoration: none;
  outline: none; }
  .btn:hover {
    color: #fff;
    background-color: #1a1a1a; }

.cookie {
  width: 100%;
  padding: 0.5em;
  background-color: #ecefde;
  border-top: 1px solid #b0b0b0;
  position: fixed;
  bottom: 0;
  z-index: 100; }
  .cookie__body {
    text-align: center;
    color: #232323;
    font-size: 12px;
    margin: 10px 0; }
  .cookie__close {
    margin-top: 10px; }

input[type="text"],
input[type="email"],
input[type="number"],
input[type="tel"],
select,
textarea {
  width: 100%;
  border: 1px solid #ecefde;
  padding: 0.5em;
  margin-top: 12px;
  border-radius: 2px; }
